import request from '@/utils/request'
import axios from 'axios'

/** 
 * 
 * 
 * 
 * 
**/

export const fileApi = {
    /**
     * 
     * @param {*} _data 
     * @returns Promise
     */
    encrypt: function ({ userName, tags, file, policy }) {
        // file            String 
        // tags            List 
        // file            File 
        // policy          String (A AND B AND (C OR D))
        const data = new FormData();
        data.append('fileName', userName);
        data.append('file', file);
        data.append('tags', tags);
        data.append('policy', policy);

        return new Promise((resolve, reject) => {
            request({
                url: '/content/upload',
                method: 'post',
                headers: { 'Content-Type': 'multipart/form-data' },
                data,
                timeout: 0,
            }).then(response => {
                // {
                //     "code":200   200, ; 
                //     "msg":"success",  
                //     "data": null
                // }
                if (response.code === 200) {
                    resolve(response.data)
                }
                else {
                    reject(response)
                }
            }).catch(reject)
        })
    },

    /**
     * 
     * @param {*} _data 
     * @returns Promise
     */
    files: function ({ userName, tag, size, bookmark }) {
        const _data = { userName, tag, size, bookmark }
        // fromUserName  String  "" 
        // tag             String 
        // pageSize      Int  10
        // bookmark        String 
        const data = {
            fromUserName: _data.userName,
            tag: _data.tag,
            pageSize: _data.size || 10,
            bookmark: _data.bookmark,
        }

        return new Promise((resolve, reject) => {
            request({
                url: '/content/list',
                method: 'get',
                data,
                params: data
            }).then(response => {
                // {
                //     "code":200   200, ; 
                //     "msg":"success",  
                //     "data":{
                //          "contents":[
                //              {
                //                  "fileName":"test.txt",
                //                  "policy":"(someone:friend AND someone:family)",
                //                  "cipher":"xxx",
                //                  "tags":[
                //                      "shanghai",
                //                      "myc",
                //                      "edu",
                //                      "test"
                //                  ],
                //                  "sharedUser":"someone"
                //              }
                //          ],
                //          "bookmark":"g1AAAA...",
                //          "pageSize":10,
                //          "count":1
                //      }
                // }
                if (response.code === 200) {
                    resolve(response.data)
                }
                else {
                    reject(response)
                }
            }).catch(reject)
        })
    },

    /**
     * 
     * @param {*} _data 
     * @returns Promise
     */
    decrypt: function ({ user, cipher, sharedUser, fileName, tags }) {
        // userName   
        // fileName   
        // cipher     
        // sharedUser 
        const data = {
            userName: user,
            fileName,
            cipher,
            tags,
            sharedUser,
        }

        return new Promise((resolve, reject) => {
            request({
                url: '/content/decryption',
                method: 'post',
                data
            }).then(response => {
                // {
                //     "code":200     200, ; 
                //     "msg":null,    
                //     "data": content 
                // }
                if (response.code === 200) {
                    resolve(response.data)
                }
                else {
                    reject(response)
                }
            }).catch(reject)
        })
    },

    /**
     * 
     * @param {*} _data 
     * @returns Promise
     */
    download: function ({ fileName, sharedUser }) {
        // fileName   
        // sharedUser 
        const data = {
            fileName,
            sharedUser,
        }

        return new Promise((resolve, reject) => {
            axios.request({
                baseURL: process.env.NODE_ENV === "development" ? process.env.VUE_APP_DEV_URL : process.env.VUE_APP_PRO_URL,
                url: '/content/download',
                method: 'get',
                data,
                params: data,
                responseType: 'blob', // important
            }).then(response => {
                // {
                //     "code":200     200, ; 
                //     "msg":null,    
                //     "data":content 
                // }
                if (response.status === 200) {
                    resolve(response.data)
                }
                else {
                    reject(response)
                }
            }).catch(reject)
        })
    },

    /**
     * 
     * @param {*} _data 
     * @returns Promise
     */
    downloadCipher: function ({ userName, fileName, sharedUser }) {
        // userName   
        // fileName   
        // sharedUser 
        const data = {
            userName,
            fileName,
            sharedUser,
        }

        return new Promise((resolve, reject) => {
            axios.request({
                baseURL: process.env.NODE_ENV === "development" ? process.env.VUE_APP_DEV_URL : process.env.VUE_APP_PRO_URL,
                url: '/content/cipher',
                method: 'get',
                data,
                params: data,
                responseType: 'blob', // important
            }).then(response => {
                // {
                //     "code":200     200, ; 
                //     "msg":null,    
                //     "data":content 
                // }
                if (response.status === 200) {
                    resolve(response.data)
                }
                else {
                    reject(response)
                }
            }).catch(reject)
        })
    }
}